import React from "react";
import Card from "./Card";

const CardList = ({ data }) => {
    return (
        <div>
            {data.map((d) => {
                return (
                    <Card
                        key={d.id}
                        id={d.id}
                        name={d.name}
                        username={d.username}
                        email={d.email}
                    />
                );
            })}
        </div>
    );
};

export default CardList;
