import React from "react";
import "./Card.css";

function Card(props) {
    const { id, name, email } = props;
    return (
        <div className="tc grow bg-white-50 br3 pa3 ma2 dib bw2 shadow-5">
            <img
                alt="cats"
                src={`https://robohash.org/${id}.png?set=set4&size=250x250`}
            />
            {/* //&size=200x200 */}
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
            </div>
        </div>
    );
}

export default Card;
